
.event-wrap {
    position:relative;
    .empty {
        position:fixed;
        width: 100%;
        top: 50%;
        left: 50%;
        text-align: center;
        transform: translate(-50%, -50%);
        img {
            width: 140px;
            height: 190px;
            padding-left: 32px;
        }
        .desc {
            margin-top:20px;
            font-size: 16px;
            line-height: 1.38;
            color: #808080;
        }
    }
    .event-list {
        padding: 20px 14px;
        .event-box {
            
            .img-box {
                height: 127px;
                position: relative;
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
                border-radius: 5px;
                .img-desc {
                    position: absolute;
                    top:10px;
                    left: 10px;
                    .tag {
                        display: inline-block;
                        padding: 0 6px;
                        line-height: 18px;
                        height: 19px;
                        color: #fff;
                        font-size: 10px;
                        font-weight: 700;
                        border-radius: 6px;
                        background-color: rgba(0, 0, 0, 0.6);
                    }
                    .sub-title {
                        margin-top: 13px;
                        font-size: 12px;
                        font-weight: 700;
                        color: #fff;
                    }
                    .title {
                        margin-top: 5px;
                        font-size: 22px;
                        font-weight: 700;
                        color: #fff;
                        line-height: 1.09;
                        letter-spacing: -1px;
                    }
                }
            }
            .title-box {
                margin-top: 10px;
                .title {
                    color: #333333;
                    font-size: 14px;
                    font-weight: 700;
                    line-height: 1.43;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                }
                .date {
                    margin-top: 4px;
                    color: #808080;
                    font-size: 12px;
                    letter-spacing: -0.06px;
                }
            }
        }
        li {
            & + li {
                margin-top: 18px;
            }
            &.end {
                .img-box {
                    &::before {
                        content: '';
                        display: block;
                        height: 100%;
                        background-color:rgba(255, 255, 255, 0.6);
                    }
                    .tag {
                        opacity: .3;
                    }
                }
                .title-box {
                    .date-end {
                        color: #808080;
                        font-size: 14px;
                        font-weight: 700;
                        line-height: 1.43;
                    }
                }
            }
        }
    }
    .event-view {
        .img-box {
            img {
                width: 100%;
            }
        }
        .desc {
            padding: 20px 14px;
            color:#000;
            font-size: 14px;
            line-height: 1.57;
            letter-spacing: -0.14px;
        }
    }
}