.agree-wrap {
    padding: 30px 16px;
    h2 {
        font-size: 16px;
        font-weight: 700;
        line-height: 1.38;
        color: #000;
    }
    .text01 {
        font-size: 13px;
        line-height: 1.46;
        color: #000;
        word-break: break-all;
    }
    .gray-box {
        padding: 14px;
        border-radius: 8px;
        background-color: #f8f8f8;
    }
    .table-wrap {
        thead {
            background-color: #f8f8f8;
            td {
                text-align: center;
            }
        }
        td {
            padding: 6px 10px;
            height: 44px;
            text-align: center;
            word-break: break-all;
            font-size: 12px;
            line-height: 1.33;
            color: #000000;
            box-sizing: border-box;
            border: 1px solid #000;
        }
    }

    
    /*여백*/
    .mrt10 {
        margin-top: 10px;
    } 
   .mrt12 {
       margin-top: 12px;
   } 
   .mrt32 {
        margin-top: 32px;
    } 
}